export const getNavList = () => {
    const navArr = [
        { name: '', to: '/', icon: 'home' },
        {
            name: '', icon: 'articles', subMenu: [
                { name: '', to: '/classes', icon: 'classes' },
                { name: '', to: '/tags', icon: 'tags' }
            ]
        },
        { name: '', to: '/say', icon: 'say' },
        { name: '', to: '/timeline', icon: 'timeline' },
        { name: '', to: '/about', icon: 'about' },
    ];

    return navArr

}